












































































import { Component, Vue } from "vue-property-decorator";
//引入vuex-class
import { Getter, Action } from "vuex-class";

@Component
export default class News extends Vue {
  @Getter news: any;
  @Getter pageNo: any;
  @Getter pageTol: any;
  @Action getNews: any;

  //获取景区动态数据
  private created() {
    this.getNews();
  }
}
